:root {
  scroll-behavior: smooth;
  font-size: 16px;
}

body {
  max-width: 100rem;
  background-color: #e8e8e8;
  margin: 0 auto;
  padding-bottom: 3rem;
  font-family: Lato, sans-serif;
}

.data-cards-section {
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  margin-top: 2rem;
  display: flex;
}

.card {
  width: 70%;
  background-color: #fff;
  border-radius: 1rem;
  justify-content: space-around;
  align-items: center;
  padding: 2rem;
  display: flex;
}

.card .card-icon {
  width: 20%;
  width: 2.5rem;
  height: 2.5rem;
  color: #4251d7;
  border-radius: 50%;
  align-items: center;
  justify-items: center;
  padding: .5rem;
  font-size: 1rem;
  display: grid;
  box-shadow: 0 0 10px rgba(100, 100, 111, .3);
}

.card .card-info {
  width: 60%;
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.card .card-info p {
  margin-right: 1rem;
  font-weight: bold;
}

.people-search-section {
  width: 70%;
  background-color: #fff;
  border-radius: 1rem;
  margin: 4rem auto 0;
  padding: 2rem;
}

.people-search-section .people-search-section__sides-wrap {
  flex-direction: column;
  align-items: center;
  padding: 0 1rem;
  display: flex;
}

.people-search-section .people-search-section__sides-wrap .people-search-section__left-side {
  width: 100%;
  margin-bottom: 1rem;
}

.people-search-section .people-search-section__sides-wrap .people-search-section__left-side input, .people-search-section .people-search-section__sides-wrap .people-search-section__right-side, .people-search-section .people-search-section__sides-wrap .people-search-section__right-side select {
  width: 100%;
}

.person-field {
  border-bottom: 1px solid #e8e8e8;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 1.5rem;
  padding: 2rem 0;
  display: flex;
}

.initialsContactInfo {
  width: 70%;
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
  display: flex;
}

.initialsContactInfo .initials {
  text-transform: uppercase;
  width: 2.5rem;
  height: 2.5rem;
  color: #4251d7;
  border-radius: 50%;
  align-items: center;
  justify-items: center;
  display: grid;
  box-shadow: 0 0 10px rgba(100, 100, 111, .3);
}

.initialsContactInfo .contact-info {
  text-align: center;
}

.initialsContactInfo .contact-info p {
  margin: 0 0 .2rem;
}

.initialsContactInfo .contact-info span {
  color: #787878;
  font-size: .7rem;
}

.country {
  width: 30%;
  text-align: center;
}

@media only screen and (min-width: 600px) {
  .card {
    justify-content: space-between;
  }

  .card .card-icon {
    width: 15%;
    width: 3.5rem;
    height: 3.5rem;
    border-radius: 50%;
    font-size: 1.4rem;
  }

  .card .card-info {
    width: 85%;
  }

  .people-search-section .people-search-section__sides-wrap {
    flex-direction: row;
    align-items: flex-start;
  }

  .people-search-section .people-search-section__sides-wrap .people-search-section__left-side {
    width: 70%;
  }

  .people-search-section .people-search-section__sides-wrap .people-search-section__left-side input {
    width: 98%;
  }

  .people-search-section .people-search-section__sides-wrap .people-search-section__right-side {
    width: 30%;
  }

  .person-field {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 0;
  }

  .initialsContactInfo {
    flex-direction: row;
    gap: 0;
  }

  .initialsContactInfo .initials {
    margin-left: 2rem;
    margin-right: 2rem;
  }

  .initialsContactInfo .contact-info, .country {
    text-align: left;
  }
}

@media only screen and (min-width: 1000px) {
  .card {
    width: 30%;
  }
}

